* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

body, html {
  display: flex;
  flex-flow: column;
  height: 100%;
  font-family: 'Roboto', sans-serif; }

h1, h2 {
  text-transform: uppercase;
  color: #4e4e4e;
  font-size: 1.6em;
  font-weight: 400; }

ul, li {
  list-style: none;
  margin: 0;
  padding: 0; }

a,
a:hover,
.form-title,
.p-no-active {
  text-decoration: none; }

.column {
  display: flex;
  flex-flow: column; }

.row {
  display: flex; }

.none {
  display: none; }

strong {
  font-weight: 600; }

*:focus {}
input,
textarea,
textarea:focus,
.button:focus {
  outline:  none; }

a,
label,
label:before,
.navi__item,
.button,
.submit {
  cursor: pointer; }

