.popup {
  display: flex;
  flex-flow: column;
  min-width: 100px;
  height: auto;
  background-color: rgba(85, 92, 102, 0.87);
  position: fixed;
  border-radius: 3px;
  bottom: 15px;
  right: 10px;
  padding: 10px;
  opacity: 0;
  transition: opacity 1s;
  p {
    padding: 2px;
    color: #fff;
    margin: 0;
    font-size: .9em; } }
