header {
  display: flex;
  position: relative;
  padding: 10px 0;
  z-index: 500;
  width: 100%;
  background: url(/pic/header-big.png);
  justify-content: flex-end;
  align-items: center;
  min-height: 45px;

  .header-tabs {
    position: absolute;
    bottom: 0;
    left: 100px;
    display: flex;

    div {
      padding: 6px;
      background-color: #fff;
      border-radius: 5px 5px 0 0;
      cursor: pointer; }

    div:first-child {
      left: 54px;
      background-color: #000;
      color: #ccc;
      margin-right: 10px; }

    div:last-child {
      border-bottom: 0;
      font-weight: 600; }

    a {
      height: 100%;
      width: 100%;
      display: block;
      position: absolute;
      top: 0;
      left: 0; } } }


.a-wrap {
  flex: 1 0 AUTO; }


